import { Avatar, Box, Divider, Paper, Stack, Typography } from '@mui/material';
import { Anchor, DefinitionList, Paragraph } from '../../';
import { useDetectPrint } from '../../../use';
import { replaceKeysInString } from '../../../util';
import { IPersonal } from '../types';

export const Personal = ({
  address,
  title,
  intro,
  avatar,
  email,
  firstname,
  lastname,
  phone,
  summary,
}: IPersonal) => {
  const isPrint = useDetectPrint();

  return (
    <>
      {false && (
        <Box component={Paper} p={3}>
          <Stack spacing={2}>
            <>
              <Typography variant="h5">{title}</Typography>

              <Avatar
                src={avatar}
                alt={`${firstname} ${lastname}`}
                sx={{ width: 300, height: 300 }}
              />

              <Typography variant="h6">
                {firstname} {lastname}
              </Typography>
            </>

            {isPrint && (
              <DefinitionList
                data={{
                  Address: address,
                  Phone: phone,
                  Email: email,
                  'Social Profile': (
                    <Anchor
                      href="https://linkedin.com/in/michaelord"
                      label="linkedin.com/in/michaelord"
                    />
                  ),
                }}
              />
            )}
          </Stack>
        </Box>
      )}

      <Box component={Paper} my={2} p={3}>
        <Stack
          component="dl"
          spacing={3}
          direction={{ xs: 'column', md: 'row' }}
          justifyContent="space-between"
          alignItems="center"
          divider={<Divider orientation="vertical" flexItem />}
        >
          <Box>
            <Avatar
              src={avatar}
              alt={`${firstname} ${lastname}`}
              sx={{ width: 300, height: 300 }}
            />
          </Box>

          <Stack spacing={2}>
            {intro && (
              <Typography variant="h4" component="p">
                {replaceKeysInString(intro, { firstname })}
              </Typography>
            )}
            <Paragraph paragraphs={summary} disableMargin />
          </Stack>
        </Stack>
      </Box>
    </>
  );
};
