import { useCallback, useState } from 'react';
import { useDidMountEffect } from './useDidMountEffect';

export enum State {
  Idle = 'idle',
  Success = 'success',
  Error = 'error',
  Pending = 'pending',
}

type asyncFunction = () => Promise<unknown>;

export const useAsync = (asyncFunction: asyncFunction, immediate = true) => {
  const [status, setStatus] = useState<State>(State.Idle);
  const [value, setValue] = useState<unknown | null>(null);
  const [error, setError] = useState(null);

  // The execute function wraps asyncFunction and
  // handles setting state for pending, value, and error.
  // useCallback ensures the below useEffect is not called
  // on every render, but only if asyncFunction changes.
  const execute = useCallback(async () => {
    setStatus(State.Pending);
    setValue(null);
    setError(null);

    try {
      const response = await asyncFunction();

      if (response) {
        setValue(response);
        setStatus(State.Success);
      } else {
        setValue(response);
        setStatus(State.Error);
      }
    } catch (e: any) {
      // console.log(e.message);

      setError(e.message || 'error has ocured');
      setStatus(State.Error);
    }
  }, [asyncFunction]);

  // Call execute if we want to fire it right away.
  // Otherwise execute can be called later, such as
  // in an onClick handler.
  useDidMountEffect(() => {
    if (immediate) {
      void execute();
    }
  }, [execute, immediate]);

  return { execute, status, value, error };
};
